* {
  margin: 0;
  padding: 0;
}
.container {
}
.app-wrapper {
  color: #eee;
  background-color: #2c2c2d;
}

.header {
  text-align: center;
}
.header h1 {
  font-family: "Muli", serif;
  font-size: 3rem;
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  color: #f46036;
  text-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.4);
}
.balance {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2rem;
  text-align: center;
}

.balance h2 {
  font-family: "Baloo Da 2", serif;
  font-size: 2rem;
  font-weight: 300;
  text-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.4);
}

.balance h3 {
  font-family: "Baloo Da 2", serif;
  font-size: 2rem;
  font-weight: 300;
  text-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.4);
}

.income-expense {
  display: flex;
  justify-content: space-between;
  margin-top: 1em;
  font-family: "Muli", serif;
  text-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.4);
  text-align: center;
  width: 90%;
}

.plus {
  color: #77c987;
}

.minus {
  color: #ff716e;
}
.income-expense h3 {
  font-size: 2rem;
  font-weight: 300;
  margin-bottom: 2rem;
}

.income-expense p {
  font-size: 1rem;
  font-weight: 700;
  text-align: center;
}
.form-wrapper {
  width: 90%;
  margin: 0 auto;
}

.input-group {
  display: flex;
  flex-direction: column;
}
.input-group input:not([type="submit"]) {
  border: none;
  background: transparent;
  border-bottom: 0.2rem solid #777;
  font-family: "Baloo Da 2", serif;
  color: #eee;
  outline: none;
  transition: border-bottom 0.4s;
  font-size: 1em;
  margin-bottom: 1em;
  padding: 0.3rem;
}
.income input:not([type="submit"]):focus {
  border-bottom: 0.2rem solid #77c987;
}

.expense input:not([type="submit"]):focus {
  border-bottom: 0.2rem solid #ff716e;
}
.input-group input[type="submit"] {
  padding: 0.7rem;
  border: none;
  outline: none;
  border-radius: 0.7rem;
  font-family: "Muli", serif;
  font-weight: 700;
  letter-spacing: 0.1rem;
  color: #eee;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
  cursor: pointer;
  margin-top: 1rem;
  text-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.4);
  margin-bottom: 1em;
}
.input-group input[type="submit"]:active {
  transform: translateY(0.3rem);
}

.income input[type="submit"] {
  background-color: #77c987;
}

.expense input[type="submit"] {
  background-color: #ff716e;
}

input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.transactions {
  margin-top: 3rem;
}

.transactions h2 {
  font-family: "Baloo da 2", serif;
  font-size: 2rem;
  letter-spacing: 0.1rem;
  font-weight: 300;
  margin-bottom: 2rem;
  text-align: center;
}
.transaction-list {
  overflow: hidden;
  overflow-y: auto;
}
.transaction {
  list-style: none;
  font-family: "Baloo da 2", serif;
  letter-spacing: 0.1rem;
  color: #fff;
  border-radius: 0.3rem;
  display: flex;
  justify-content: space-between;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
  background: red;
  width: 75%;
  margin-left: 8px;
  padding: 8px;
}
.transactions-income .transaction {
  background-color: #5a9b67;
}

.transactions-expense .transaction {
  background-color: #cf5856;
}

.transaction-text {
}

.transaction-amount {
  transform: translateX(3rem);
}
.delete-btn {
  transform: translateX(4rem);
  border: none;
  background-color: transparent;
  font-size: 1rem;
  margin: 0 1rem;
  color: #d4403e;
  text-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.4);
  cursor: pointer;
  outline: none;
}
